import React, { Component } from 'react';
import './SignUp.css';
import 'tachyons';
import _ from 'lodash';
import { connect } from 'react-redux';
import { GoogleLogin, GoogleLogout } from 'react-google-login';
import { push } from 'react-router-redux';
import agent from '../../agent';
import { store } from '../../store';

class SignUp extends Component {
  constructor() {
    super();
    this.state = {
      user: {
        firstname: '', lastname: '', phone: '', gender: 'M', birthday: '',
      },
      showSignup: false,
    };
    this.signup = this.signup.bind(this);
  }

    responseGoogle = (response) => {
      if (response.profileObj) {
        const { givenName, familyName, email } = response.profileObj;
        console.log(response.profileObj);
        this.props.setUser(response.profileObj)
        this.setState({
          user: {
            ...this.state.user, firstname: givenName, lastname: familyName, email,
          },
        });
        const data = {
          email: this.state.user.email,
        };
        console.log(data);

        agent.Auth.confirmEmail(data).then((res) => {
          console.log(res);
          if (res.data) {
            if (res.data.data) {
              if (res.data.data.exists) {
                window.localStorage.setItem('bearer', res.data.data.token);
                agent.setToken(res.data.data.token);
                store.dispatch(push('/home'));
              } else {
                this.setState({ showSignup: true });
              }
            }
          }
        }).catch((err) => {
          console.log(err);
        });
      }
    }

    signup(e) {
      e.preventDefault();

      const {
        firstname, lastname, phone, email, birthday, gender,
      } = this.state.user;
      let date = birthday.split('-');
      date = `${date[2]}-${date[1]}-${date[0]}`;
      const data = {
        email,
        phone,
        gender,
        birthday: date,
        name: `${firstname} ${lastname}`,
      };
      console.log(data);
      agent.Auth.register(data).then((res) => {
        console.log(res);
        store.dispatch(push('/home'));
      }).catch((err) => {
        console.log(err.response);
      });
    //   console.log(res);
    }

    render() {
      return (
        <div>
          <div className="">
            {
                  !this.state.showSignup
                    ? (
                      <GoogleLogin
                        clientId="928896030573-2biu76d0mtbjdsnh2rs4mjahaebi0am8.apps.googleusercontent.com"
                        className="google-btn"
                        buttonText="Login"
                        onSuccess={this.responseGoogle}
                        onFailure={this.responseGoogle}
                        cookiePolicy="single_host_origin"
                        redirectUri="https://extremist.team/oauth/complete/google"
                        render={(renderProps) => (
                          <button className="social-signin google" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                                 Log in with Google
                          </button>
                        )}
                      />
                    ) : (
                      <div>
                        {' '}
                        <h4>Fill Up the information</h4>
                        <form onSubmit={this.signup}>
                          <label>First Name</label>
                          <input type="text" className="text-input" name="first-name" placeholder="First Name" onChange={(e) => this.setState({ user: { ...this.state.user, firstname: e.target.value } })} required value={this.state.user.firstname} />

                          <label>Last Name</label>
                          <input type="text" name="last-name" placeholder="Last Name" className="text-input" onChange={(e) => this.setState({ user: { ...this.state.user, lastname: e.target.value } })} required value={this.state.user.lastname} />

                          <label>Birthday</label>
                          <input
                            type="date"
                            name="birthday"
                            placeholder="Birthday"
                            className="text-input"
                            onChange={(e) => this.setState({ user: { ...this.state.user, birthday: e.target.value } })}
                            required
                            value={this.state.user.birthday}
                          />

                          <label>Gender</label>
                          <select id="gender" name="gender" className="text-input" onChange={(e) => this.setState({ user: { ...this.state.user, gender: e.target.value } })} defaultValue={this.state.user.gender}>
                            <option value="M">Male</option>
                            <option value="F">Female</option>

                          </select>


                          <label>Email</label>
                          <input type="text" name="email" disabled={this.state.showSignup} placeholder="E-mail" className="text-input" onChange={(e) => this.setState({ user: { ...this.state.user, email: e.target.value } })} required value={this.state.user.email} />

                          <label>Phone number</label>
                          <input
                            type="number"
                            name="phone"
                            placeholder="Phone Number"
                            className="text-input"
                            onChange={(e) => this.setState({ user: { ...this.state.user, phone: e.target.value } })}
                            required
                            value={this.state.user.phone}
                          />
                          {/*
                  <label>Password</label>

                  <input
                    type="password"
                    name="password"
                    placeholder="Password"
                    className="text-input"
                  /> */}
                          <input type="submit" name="signup_submit" value="Sign me up" />
                        </form>
                      </div>
                    )
}


          </div>
        </div>
      );
    }
}

// console.log("in the admin mstp func", state)
const mapStateToProps = (state) => ({

});
const mapDispatchToProps = (dispatch) => ({
  setUser:(user) => dispatch({type:"SET_USER",payload:user})
});

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);
